    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Los ajustes en la gestión del servicio turístico con enfoque étnico y sostenible permiten adaptar las experiencias a las realidades culturales, ambientales y sociales de cada territorio. Los ajustes de contenido buscan enriquecer las narrativas, visibilizando nuevas voces -como las de mujeres y jóvenes guardianes del conocimiento- y eliminando estereotipos, siempre en coherencia con las cosmovisiones locales. Los ajustes logísticos optimizan la experiencia mediante la reorganización de horarios, rutas y puntos de descanso, garantizando bienestar y sostenibilidad ambiental. En el ámbito procedimental, se fortalecen los protocolos de bienvenida, la participación comunitaria y la formación de los guías locales, promoviendo un acompañamiento respetuoso y coordinado. Finalmente, los ajustes culturales aseguran la armonía con las normas internas de cada pueblo, mediante el diálogo constante con autoridades tradicionales y la sensibilidad hacia las diferencias de género y rol social. En conjunto, estos ajustes consolidan un turismo más inclusivo, participativo y respetuoso de la diversidad cultural.
      
    
